@import "mixins";
@media screen and (max-width:1200px) {
  h1, h4, h5, p, a, i, button, span, address, li, label, small {
    font-size: 15px; }
  h2, h3 {
    font-size: 2em; }
  .h1 {
    font-size: 1.8em; }
  .h2 {
    font-size: 1.3em; }
  .absolute-width {
    width: 400px; }
  .group {
    margin-bottom: 20px; }
  .button-form {
    font-size: .9em; }
  .globalnav {
    &__logo {
      width: 95px; } }
  .head-offer {
    &__ball {
      height: 9vh; } }
  .half-form {
    &__half-ball {
      width: 25px; }
    &__inform-first {
      padding: 35px 40px; }
    .political {
      margin-bottom: 20px; } }
  .learn {
    &__form-first {
      padding: 25px; } }
  .contacts {
    padding: 70px 0; }
  .footer {
    &__logo {
      width: 75px; }
    &__school-name {
      font-size: 0.77em; }
    &__rights {
      max-width: 153px; } } }
@media screen and (max-width:992px) {
  h1, h4, h5, p, a, i, button, span, address, li, label, small {
    font-size: 13px; }
  .h1 {
    font-size: 1.4em; }
  .h2 {
    font-size: 1em; }
  .absolute-width {
    width: 300px; }
  .button-mod {
    font-size: .9em;
    padding: 6px 16px; }
  .button-form {
    font-size: .7em; }
  .group {
    margin-bottom: 15px; }
  .globalnav {
    &__logo {
      width: 75px; }
    &__text-logo {
      font-size: 0.67em; }
    &__contact {
      padding: 7px 17px; } }
  .head-offer {
    &__svgball {
      height: 30px;
      width: 30px; }
    &__offerbach {
      p {
        padding: 13px 0; } } }
  .instagram {
    &__link, &__button {
      margin: 55px auto; } }
  .learn {
    &__half-ball {
      width: 300px; } }
  .half-form {
    &__inform-second {
      padding: 10px 0; }
    &__form {
      .select-cokes {
        font-size: 1em; } } }
  .form-last {
    &__sub-head {
      padding: 10px 0 20px; } }
  .footer {
    &__logo {
      width: 55px; }
    &__school-name {
      font-size: 0.57em; } } }
@media screen and (max-width:768px) {
  h1, h4, h5, p, a, i, button, span, address, li, label, small {
    font-size: 11px; }
  h2, h3 {
    font-size: 1.7em; }
  .political [type="checkbox"] + label {
    &:before {
      margin-top: 0; }
    &:after {
      margin-top: 1px; } }
  .h1 {
    font-size: 1.2em; }
  .learn {
    p {
      font-size: 0.9em; } }
  .globalnav {
    address {
      display: none; }
    &__logo {
      width: 60px; }
    &__text-logo {
      font-size: 0.6em; } }
  .footer {
    &__social {
      a {
        width: 23px; } }
    &__school-name {
      font-size: 0.37em; }
    &__logo {
      width: 45px;
      margin-right: 1px; }
    &__rights {
      max-width: 113px; } } }
@media screen and (max-width:575.98px) {
  h1, h4, h5, p, a, i, button, span, address, li, label, small {
    font-size: 9px; }
  .globalnav {
    &__number-mob {
      svg {
        width: 12px;
        vertical-align: middle;
        #white-power-top, #white-power-top-2 {
          fill: #E9E9E9; } } } }
  .head-offer {
    height: auto;
    &__ball {
      position: absolute;
      top: 0;
      right: 0;
      text-align: right;
      padding: 0; }
    &__bat {
      height: auto;
      width: 100%; }
    &__offerbach {
      margin-bottom: 50px; }
    &__mob-offer {
      margin-bottom: 30px; } } }
